import Vue from 'vue';
import { translate as t } from '@nextcloud/l10n';
import { showError, showSuccess } from '@nextcloud/dialogs';
import NcActionButton from '@nextcloud/vue/dist/Components/NcActionButton.js';
import NcActionInput from '@nextcloud/vue/dist/Components/NcActionInput.js';
import NcActions from '@nextcloud/vue/dist/Components/NcActions.js';
import NcActionSeparator from '@nextcloud/vue/dist/Components/NcActionSeparator.js';
import ArrowLeft from 'vue-material-design-icons/ArrowLeft.vue';
import CalendarClock from 'vue-material-design-icons/CalendarClock.vue';
import Check from 'vue-material-design-icons/Check.vue';
import CloseCircleOutline from 'vue-material-design-icons/CloseCircleOutline.vue';
import { clearReminder, setReminder } from '../services/reminderService.ts';
import { DateTimePreset, getDateString, getDateTime, getInitialCustomDueDate, getVerboseDateString } from '../shared/utils.ts';
import { logger } from '../shared/logger.ts';
const laterToday = {
  dateTimePreset: DateTimePreset.LaterToday,
  label: t('files_reminders', 'Later today'),
  ariaLabel: t('files_reminders', 'Set reminder for later today')
};
const tomorrow = {
  dateTimePreset: DateTimePreset.Tomorrow,
  label: t('files_reminders', 'Tomorrow'),
  ariaLabel: t('files_reminders', 'Set reminder for tomorrow')
};
const thisWeekend = {
  dateTimePreset: DateTimePreset.ThisWeekend,
  label: t('files_reminders', 'This weekend'),
  ariaLabel: t('files_reminders', 'Set reminder for this weekend')
};
const nextWeek = {
  dateTimePreset: DateTimePreset.NextWeek,
  label: t('files_reminders', 'Next week'),
  ariaLabel: t('files_reminders', 'Set reminder for next week')
};
export default Vue.extend({
  name: 'SetReminderActions',
  components: {
    ArrowLeft,
    CalendarClock,
    Check,
    CloseCircleOutline,
    NcActionButton,
    NcActionInput,
    NcActions,
    NcActionSeparator
  },
  props: {
    file: {
      type: Object,
      required: true
    },
    dueDate: {
      type: Date,
      default: null
    }
  },
  data() {
    return {
      open: true,
      now: new Date(),
      customDueDate: getInitialCustomDueDate()
    };
  },
  watch: {
    open(isOpen) {
      if (!isOpen) {
        this.$emit('close');
      }
    }
  },
  computed: {
    fileId() {
      return this.file.id;
    },
    fileName() {
      return this.file.name;
    },
    clearAriaLabel() {
      return "".concat(t('files_reminders', 'Clear reminder'), " \u2013 ").concat(getVerboseDateString(this.dueDate));
    },
    customAriaLabel() {
      if (this.customDueDate === '') {
        return null;
      }
      return "".concat(t('files_reminders', 'Set reminder at custom date & time'), " \u2013 ").concat(getVerboseDateString(this.customDueDate));
    },
    options() {
      const computeOption = option => {
        const dateTime = getDateTime(option.dateTimePreset);
        if (!dateTime) {
          return null;
        }
        return {
          ...option,
          ariaLabel: "".concat(option.ariaLabel, " \u2013 ").concat(getVerboseDateString(dateTime)),
          dateString: getDateString(dateTime),
          action: () => this.set(dateTime)
        };
      };
      const options = [laterToday, tomorrow, thisWeekend, nextWeek];
      return options.map(computeOption).filter(Boolean);
    }
  },
  methods: {
    t,
    getDateString,
    async set(dueDate) {
      try {
        await setReminder(this.fileId, dueDate);
        showSuccess(t('files_reminders', 'Reminder set for "{fileName}"', {
          fileName: this.fileName
        }));
        this.open = false;
      } catch (error) {
        logger.error('Failed to set reminder', {
          error
        });
        showError(t('files_reminders', 'Failed to set reminder'));
      }
    },
    async setCustom() {
      // Handle input cleared
      if (this.customDueDate === '') {
        showError(t('files_reminders', 'Please choose a valid date & time'));
        return;
      }
      try {
        await setReminder(this.fileId, this.customDueDate);
        showSuccess(t('files_reminders', 'Reminder set for "{fileName}"', {
          fileName: this.fileName
        }));
        this.open = false;
      } catch (error) {
        logger.error('Failed to set reminder', {
          error
        });
        showError(t('files_reminders', 'Failed to set reminder'));
      }
    },
    async clear() {
      try {
        await clearReminder(this.fileId);
        showSuccess(t('files_reminders', 'Reminder cleared'));
        this.open = false;
      } catch (error) {
        logger.error('Failed to clear reminder', {
          error
        });
        showError(t('files_reminders', 'Failed to clear reminder'));
      }
    }
  }
});